import React, { useContext } from 'react'
import "./About.css"
import Award from "../../img/Award.jpg"
import { ThemeContext } from '../../Context'

const About = () => {
    
    const theme = useContext(ThemeContext)
    return (
        <div className="a">
            <div className="a-left">
                <div className="a-card bg" style={{backgroundColor:theme.state.darkMode? "#d3d8d3":"#48494b"}}></div>
                <div className="a-card">
                    <img src="https://static.independent.co.uk/s3fs-public/thumbnails/image/2015/10/19/11/attack-on-titan.jpg" alt="" className="a-img" />
                </div>
            </div>
            <div className="a-right">
                <h1 className="a-title">About Me</h1>
                <p className="a-sub">This is some subtitle about me</p>
                <p className="a-desc">Lorem ipsum dolor sit amet consectetur adipisicing elit. Quisquam iste quam consequatur nostrum aliquam soluta optio similique officiis? Rem sunt suscipit nobis deserunt odit voluptatibus ab necessitatibus quo voluptates perspiciatis! </p>
                <div className="a-award">
                    <img src={Award} alt="" className="a-award-img" />
                    <div className="a-award-text">
                        <h4 className="a-award-title">Some Design Award</h4>
                        <div className="a-award-desc">Lorem ipsum dolor sit amet consectetur adipisicing elit.</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default About
