import React, { useContext, useRef, useState } from 'react'
import "./Contact.css"
import phone from "../../img/phone.png"
import email from "../../img/email.png"
import address from "../../img/address.png"
import emailjs from '@emailjs/browser';
import { ThemeContext } from '../../Context'


const Contact = () => {
    const formRef = useRef()
    const [done, setdone] = useState(false)
    const theme = useContext(ThemeContext)
    const darkMode = theme.state.darkMode
    

    const handleSubmit = (e)=>{
        e.preventDefault();
        e.preventDefault();

        emailjs.sendForm('service_xkaddog', 'template_aiwuhd8', formRef.current, "user_MSnsmauDLYm4J3WZgAanV")
          .then((result) => {
              console.log(result.text);
              setdone(true)
          }, (error) => {
              console.log(error.text);
          });

    }

    return (
        <div className='c'>
            <div className="c-bg" style={{backgroundColor:darkMode? "#59b256":"#3d87db"}}></div>
            <div className="c-wrapper">
                <div className="c-left">
                    <h1 className="c-left-title">Let's Discuss your project</h1>
                    <div className="c-info">
                        <div className="c-info-item">
                            <img src={phone} alt="" className="c-icon" />
                            +918920938409
                        </div>
                        <div className="c-info-item">
                            <img src={email} alt="" className="c-icon" />
                            sagarbaisoya1@gmail.com
                        </div>
                        <div className="c-info-item">
                            <img src={address} alt="" className="c-icon" />
                            Farm Stay, Annangpur, Faridabad, Haryana, India 121003
                        </div>
                    </div>
                </div>
                <div className="c-right">
                    <p className="c-right-desc">
                        <b>Whats your story?</b> Get in touch. Always ready for freelancing if the right project comes along.
                    </p>
                    <form ref={formRef} onSubmit={handleSubmit}>
                        <input style={{backgroundColor:darkMode && "#333"}}  type="text" placeholder='Name' name="user_name"/>
                        <input style={{backgroundColor:darkMode && "#333"}}  type="text" placeholder='Subject' name="user_subject"/>
                        <input style={{backgroundColor:darkMode && "#333"}}  type="text" placeholder='Email' name="user_email"/>
                        <textarea style={{backgroundColor:darkMode && "#333"}} rows="5" placeholder='Message' name="message"/>
                        <button>Submit</button>
                        {done && "Thank you..."}
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact
