import React, { useContext } from 'react'
import "./Intro.css"
import Me2 from "../../img/Me2.png"
import { ThemeContext } from '../../Context'

const Intro = () => {
    const theme = useContext(ThemeContext)
    return (
        <div className="i">
            <div className="i-left">
                <div className="i-left-wrapper">
                    <h2 className="i-intro">Hello, My name is</h2>
                    <h1 className="i-name">Sagar Baisoya</h1>
                    <div className="i-title">
                        <div className="i-title-wrapper">
                            <div className="i-title-item" style={{color:theme.state.darkMode?"#59b256":"#3d87db"}}>Web Developer</div>
                            <div className="i-title-item" style={{color:theme.state.darkMode?"#59b256":"#3d87db"}}>UI/UX Designer</div>
                            <div className="i-title-item" style={{color:theme.state.darkMode?"#59b256":"#3d87db"}}>Photographer</div>
                            <div className="i-title-item" style={{color:theme.state.darkMode?"#59b256":"#3d87db"}}>3D Home Designer</div>
                            
                        </div>
                        
                    </div>
                    <p className="i-desc">Lorem ipsum dolor sit amet consectetur adipisicing elit. In, doloremque! Omnis quia ipsum tenetur possimus debitis ipsam vitae vero nisi dolorum dolor reiciendis, pariatur quaerat velit quae fuga laboriosam libero.</p>
                </div>
            </div>
            <div className="i-right">
                <div className="i-bg" 
                style={{backgroundColor:theme.state.darkMode? "#59b256":"#3d87db"}}
                ></div>
                <img src={Me2} alt="" className="i-img" />
            </div>

        </div>
    )
}

export default Intro
